<template>
  <section>
  <el-row>
      <el-col :span="24">
        <span class="top-nav-link" @click="goBack">返回上一页</span>
        <!-- <span class="top-nav-link">重新选择症状</span>
        <span class="top-nav-link">论治</span> -->
      </el-col>
  </el-row>
  <el-row>
    <el-col :span="20">
      <el-form label-width="40px">
        <el-row>
          <el-col :span="7">
            <el-form-item label="主症">
              <el-input readonly="" v-model="zy_mainSymptomName"></el-input>
            </el-form-item>
          </el-col>
          <el-col :offset="1" :span="8">
            <el-form-item label="兼症">
              <div>
                <el-input readonly v-model="zy_diseaseName" style="position: absolute;" :rows="4" type="textarea"></el-input>
              </div>
