          </el-col>
          <el-col :offset="1" :span="7">
            <el-form-item label="舌相">
              <el-input readonly v-model="zy_tongueName"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="脉相">
              <el-input readonly v-model="zy_pulseName"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </el-col>
    <el-col :span="4">
      <img :src="imgSrc" style="width: 120px; margin-left: 12px;">
    </el-col>
  </el-row>
  <el-row class="border-box">
    <el-col align="center">
      <span class="box-title">论治结果</span>
    </el-col>
    <el-col style="margin-top: 8px;">
      <el-table
      ref="yfTable"
      @select="handelYaoFangTableSelection"
      :data="lzData">
        <el-table-column type="selection"></el-table-column>
        <el-table-column label="方剂名称" prop="prescriptionName"></el-table-column>
        <el-table-column label="推荐" prop="subID"></el-table-column>
        <el-table-column label="症型" prop="dialecticalName"></el-table-column>
        <el-table-column label="治法" prop="cureName"></el-table-column>
        <el-table-column label="方解">
          <!-- 药解弹窗 -->
          <template slot-scope="scope">
            <el-popover
              placement="left-end"
              width="420"
              trigger="hover">
              <div>
                <div>【方解】 {{fangjie.calcite}}</div>
                <div>【来源】 {{fangjie.source}}</div>
                <div>【功效】 {{fangjie.effect}}</div>
                <div>【主治】 {{fangjie.attend}}</div>
              </div>
              <img @mouseover="handelGetFangJie(scope.row)" slot="reference" src="../../assets/imgs/explain.png" class="chufang-icon">
            </el-popover>
          </template>
        </el-table-column>
      </el-table>
    </el-col>
  </el-row>
  <!-- 下面来自zhijie -->
    <div class="chufang">
    <el-row :gutter="2">
      <el-col :span="4" :offset="1" class="tag-select">
        <span @click="handelTagChange($event,0)" class="active" >处方</span>
        <span @click="handelTagChange($event,1)">标准处方</span>
      </el-col>
      <el-col :span="12" class="chufang-center" align="center">
        <span style="white-space:pre"> {{hpwrData}}</span>
      </el-col>
      <el-col :span="6" style="text-align: right">
        <el-button type="primary" size="small" icon="el-icon-plus" @click="handleAdd()">添加</el-button>
        <!-- <el-button type="primary" size="small" icon="el-icon-minus" @click="handleRemove()">删除</el-button> -->
      </el-col>
    </el-row>
    <el-row v-if="tagWindowShow">
      <el-col>
        <el-table
        :data="chufangItemData"
        style="width: 100%;margin-top:10px;" height="320">
          <!-- <el-table-column type="selection"></el-table-column> -->
          <el-table-column type="index" width="50"></el-table-column>
          <el-table-column prop="drugName" label="药名"></el-table-column>
          <el-table-column prop="quanitity" label="数量"></el-table-column>
          <el-table-column :formatter="elFormatYaoXing" prop="drugTaste" label="药性"></el-table-column>
          <el-table-column label="药解">
            <!-- 药解弹窗 -->
            <template slot-scope="scope">
              <el-popover
                placement="left-end"
                width="420"
                trigger="hover">
                <div>
                  <div>【别名】 {{yaojie[2]}}</div>
                  <div>【来源】 {{yaojie[3]}}</div>
                  <div>【性味】 {{yaojie[0]}}</div>
                  <div>【归经】 {{yaojie[1]}}</div>
                  <div>【功能】 {{yaojie[4]}}</div>
                  <div>【主治】 {{yaojie[5]}}</div>
                  <div>【用法用量】 {{yaojie[6]}}</div>
                  <div>【特殊用法】 {{yaojie[7]}}</div>
                  <div>【注意事项】 {{yaojie[8]}}</div>
                  <div>【贮藏】 {{yaojie[9]}}</div>
                </div>
                <img @mouseover="handelGetYaoJie(scope.row)" slot="reference" src="../../assets/imgs/explain.png" class="chufang-icon">
              </el-popover>
            </template>
          </el-table-column>
          <el-table-column width="120px" label="操作" header-align="center">
            <template slot-scope="scope">
              <el-button-group>
                <span class="icon-button-img" @click="handleCayoYaoEdit(scope.row)"><img src="../../assets/imgs/edit.png"></span>
                <span class="icon-button-img" @click="handleRemoveCYitem(scope.$index)" ><img src="../../assets/imgs/delete.png"></span>
              </el-button-group>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
        <el-row v-if="!tagWindowShow">
      <el-col>
        <el-table
        :data="yaofangItem"
        style="width: 100%;margin-top:10px;" height="320">
          <!-- <el-table-column type="selection"></el-table-column> -->
          <el-table-column type="index" width="50"></el-table-column>
          <el-table-column prop="drugName" label="药名"></el-table-column>
          <el-table-column prop="quanitity" label="数量"></el-table-column>
          <el-table-column :formatter="elFormatYaoXing" prop="drugTaste" label="药性"></el-table-column>
          <el-table-column label="药解">
            <!-- 药解弹窗 -->
            <template slot-scope="scope">
              <el-popover
                placement="left-end"
                width="420"
                trigger="hover">
                <div>
                  <div>【别名】 {{yaojie[2]}}</div>
                  <div>【来源】 {{yaojie[3]}}</div>
                  <div>【性味】 {{yaojie[0]}}</div>
                  <div>【归经】 {{yaojie[1]}}</div>
