    </div>
    <!-- 图表开始 -->
    <e-chart ref="echart"></e-chart>
    <!-- 下部表单开始 -->
    <el-form label-width="100px">
      <el-row style="margin-top:24px">
        <el-col :span="10">
            <el-form-item label="处方名称:">
              <el-input v-model="prescriptionName" readonly></el-input>
            </el-form-item>
        </el-col>
        <el-col :span="2">
          <el-form-item label="代煎:">
          <el-checkbox v-model="checked"></el-checkbox>
          </el-form-item>
        </el-col>
        <el-col :span="4">
          <el-form-item label="付数:">
            <el-input v-model="bl_usageCount"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="4">
