              </el-input>
            </el-form-item>
            <el-form-item label="中医病名:">
              <el-input v-model="bl_chineseMedicineName" placeholder="请选择中医病名[可选]" readonly="">
                <el-button slot="append" icon="el-icon-search" @click="handeChineseMedicine()"></el-button>
              </el-input>
            </el-form-item>
