              </el-input>
            </el-form-item>
          </el-col>
      </el-row>
    </el-form>
    <!-- 下部按钮栏 -->
    <bottom-bar ref="bottomBar" @jingyanPushItem="jingyanAddData" :yaoList="chufangItemData" :data="caseSaveData"></bottom-bar>
    <!-- 弹出框 -->
    <!-- 弹出框 草药增加   -->
    <el-dialog
      title="添加草药"
      :visible.sync="dialog_caoyaotianjia"
      width="60%"
      center>
      <el-row>
        <el-form label-width="100px">
          <el-col :span="12">
            <el-form-item label="草药名称:">
              <el-input placeholder="请选择草药" v-model="caoyao.drugName" readonly>
                <el-button slot="append" icon="el-icon-search"
                 @click="handleAddCaoyao()"></el-button>
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item label="数量:">
              <el-input placeholder="请填写数量" v-model="caoyao.quanitity">
                <template slot="append">g</template>
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="22">
            <el-form-item label="煎药方法:">
              <el-select v-model="caoyao.sufferTypeCode" placeholder="请选择煎药方法" style="width:100%">
                <el-option
                  v-for="item in jianyaoOptions"
                  :key="item.sufferTypeCode"
                  :label="item.sufferTypeName"
                  :value="item.sufferTypeCode">
                </el-option>
              </el-select>
