          </el-col>
          <el-col :span="22">
            <el-form-item label="选择:">
              <el-radio-group v-model="yaoxingData" @change="radioChange">
                <el-radio :label="1">君</el-radio>
                <el-radio :label="2">臣</el-radio>
                <el-radio :label="3">佐</el-radio>
                <el-radio :label="4">使</el-radio>
