          </el-col>
        </el-form>
      </el-row>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialog_caoyaotianjia = false">取 消</el-button>
        <el-button type="primary" @click="handelAddCaoYaoItem">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 弹出框 草药选择 -->
    <el-dialog
      :visible.sync="dialog_caoyao"
      width="60%"
      center>
      <el-row>
        <el-col :span="14" class="tab-btn">
          <span data-index='3' class="tab-btn-active" @click="handelTypeChange">全部</span>
          <span data-index='0' @click="handelTypeChange">性味药</span>
          <span data-index='1' @click="handelTypeChange">引经药</span>
        </el-col>
        <el-col :span="10" style="text-align:right">
          <el-input @keydown.enter.native="handelSearchCY" @blur="handelSearchCY"
           placeholder="输入您想查询的编码、名称、拼音码" v-model="caoyaoSelectQuery">
            <el-button slot="append" icon="el-icon-search" @click="handelSearchCY"></el-button>
          </el-input>
        </el-col>
        <el-col :span="24" class="select-caoyao" v-if="caoyaoYaoXing.length > 0" >
          <div class="caoyao-title">性味勾选</div>
          <div class="caoyao-item">
            <el-checkbox-group @change="handelYaoXingChange" v-model="checkList" class="caoyao-yaoxing-list">
              <el-checkbox
              :label="item.tasteCode?item.tasteCode +':' +item. tasteName: item.meridianCode + ':' + item.meridianName"
              v-for="item in caoyaoYaoXing"
              :key="item.meridianCode"></el-checkbox>
            </el-checkbox-group>
          </div>
        </el-col>
      </el-row>
      <el-row style="margin-top: 20px;">
        <el-col :span="24">
          <el-table
          height="420"
          :data="caoyaoData"
          @row-dblclick="select_item"
          style="width: 100%">
              <el-table-column
                prop="drugName"
                label="中药名称"
                width="100">
              </el-table-column>
              <el-table-column
                prop="HISCorreCode"
                label="HIS编码"
                width="100">
              </el-table-column>
              <el-table-column
                width="80"
                prop="inputCode"
                label="拼音码">
              </el-table-column>
              <el-table-column
                prop="otherName"
                label="别名">
              </el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </el-dialog>
    <!-- 弹出框 用法用量 -->
    <el-dialog title="用量用法" :visible.sync="dialog_yongfa" width="60%" center="">
      <el-form label-width="100px">
        <el-row :gutter="1">
            <el-form-item label="煎法:">
              <el-col :span="4">
                <el-input v-model="bl_yongfa_window.ri">
                  <template slot="append">日</template>
                </el-input>
              </el-col>
              <el-col :span="4" :offset="1">
                <el-input v-model="bl_yongfa_window.fu">
                  <template slot="append">付</template>
                </el-input>
              </el-col>
              <el-col :span="4" :offset="1">
                <el-input v-model="bl_yongfa_window.ci">
                  <template slot="append">次</template>
                </el-input>
              </el-col>
            </el-form-item>
            <el-form-item label="备注:">
              <el-col :span="20">
                <el-input type="textarea" v-model="bl_yongfa_window.zhu" :rows="6"></el-input>
              </el-col>
            </el-form-item>
            <el-col style="margin-top: 20px;text-align:center">
              <el-button type="primary" @click="dialog_yongfa=false">确 定</el-button>
            </el-col>
        </el-row>
      </el-form>
    </el-dialog>
    <!-- 弹出框 西药病名 -->
    <el-dialog :visible.sync="dialog_xiyao" width="60%">
      <el-row>
        <el-col :span="12" :offset="6">
          <el-input
          clearable
          v-model="searchXY"
          @blur="handelSearchXY"
          @keydown.enter.native="handelSearchXY"
          placeholder="请输入您想要查询的病名或拼音码" prefix-icon="el-icon-search"></el-input>
        </el-col>
        <el-col style="margin-top: 26px">
          <el-table
          height="480"
            @row-dblclick="handelSelectXiYi"
            :data="xyData"
            style="width: 100%">
            <el-table-column
              header-align="center"
              align="center"
              width="120"
              prop="westernMedicineCode"
              label="西医编码">
            </el-table-column>
            <el-table-column
              header-align="center"
              align="center"
              prop="westernMedicineName"
              label="西医病名">
            </el-table-column>
            <el-table-column
              header-align="center"
              align="center"
              prop="pinYinCode"
              label="拼音码">
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </el-dialog>
    <!-- 中医病名 -->
    <el-dialog :visible.sync="dialog_chineseMedicine" width="60%">
      <el-row>
        <el-col :span="12" :offset="6">
          <el-input
          clearable
           v-model="searchZY"
           @blur="handelSearchZY"
          @keydown.enter.native="handelSearchZY"
           placeholder="请输入您想要查询的病名或拼音码" prefix-icon="el-icon-search"></el-input>
        </el-col>
        <el-col style="margin-top: 26px">
          <el-table
            @row-dblclick="handelSelectZYi"
            :data="zyData"
            height="480"
            style="width: 100%">
            <el-table-column
              header-align="right"
              align="right"
              width="120"
              prop="chineseMedicineCode"
              label="中医编码">
            </el-table-column>
            <el-table-column
              header-align="center"
              align="center"
              prop="chineseMedicineName"
              label="中医病名">
            </el-table-column>
            <el-table-column
              header-align="center"
              align="center"
              prop="pinYinCode"
              label="拼音码">
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </el-dialog>
    <!-- 弹框 医嘱模板 -->
    <el-dialog :visible.sync="dialog_yizhu" width="50%">
      <el-row>
        <el-col :span="10" :offset="1">
          <h3 class="yizhu-title">医嘱模板</h3>
        </el-col>
        <el-col :span="10" :offset="1">
          <h3 class="yizhu-title">编辑文本</h3>
        </el-col>
      </el-row>
      <el-row style="margin-top:24px">
        <el-col :span="10" :offset="1">
          <el-input suffix-icon="el-icon-search"></el-input>
          <!-- 医嘱表格 -->
          <el-table height="380" :data="yzData" :show-header="false" style="margin-top: 12px;">
            <el-table-column
              prop="usedPhrasesName"
              label="项目">
            </el-table-column>
            <el-table-column
              align="right"
              prop="name"
              label="项目">
                  <template slot-scope="scope">
                    <img class="arrow-right" src="../../assets/imgs/arrow.png" @click="handelAddYizhu(scope.row)">
                  </template>
            </el-table-column>
          </el-table>
        </el-col>
        <el-col :span="10" :offset="1">
          <el-input type="textarea" :rows="20" v-model="bl_yizhu"></el-input>
        </el-col>
        <el-col :span="20" align="center" style="margin-top: 20px;">
          <el-button type="primary" @click="dialog_yizhu = false">确 定</el-button>
        </el-col>
      </el-row>
    </el-dialog>
  </section>
</template>
<script>
import echart from '../../components/echarts'
import bottom_bar from '../../components/bottom_bar'
import * as fun from '../../util/fun'
import {
  searchChineseMedicineList,
  getAllWesternMedicineName,
  getModelTable,
  getSufferType, // 获取煎药方法
  getTasteOrMeridian, // 草药药性分类
  getDrugDictList, // 获取药材
  getDrugWayTable, // 用药方式
  getDrugExplanation, // 获取药解
  updateEmpBasic, // 保存病例
  forHRWPData, // 药房寒热等信息
  forDTZXTData, // 草药/药方折线图
  saveOrFinishVisit, // 保存/完成就诊
  getGovernanceResult, // 获取论治结果
  getCalciteByPrescriptionCode, // 获取方解
  getDrugByPrescriptionCode, // 药方获取药材
} from '../../api'
import {mapState} from 'vuex'
export default {
  components:{
    'e-chart': echart,
    'bottom-bar': bottom_bar
  },
  watch:{
    chufangItemData:function(){
      this.handelYaoFangExpData()
      if(this.yaofangItem.length == 0){
        this.$refs.yfTable.clearSelection()
        this.rowSign = {}
        this.prescriptionName = ''
      }
    }
  },
  computed:{
    ...mapState('patient',{
      empBasicMainID:state=>state.empBasicMainID,
      registrationNum:state=>state.registrationNum,
      deptCode:state=>state.deptCode,
    }),
    ...mapState('caseNote',{
      pro_cureCode:state=>state.cureCode,
      pro_dialecticalCode:state=>state.dialecticalCode,
    }),
    caseSaveData(){ // 病例待保存数据
      return{
        empBasicMainID: this.empBasicMainID,
        registrationNum : this.registrationNum, // 挂号流水号
        dialecticalCode: this.pro_dialecticalCode, // 症型编码
        cureCode: this.pro_cureCode, // 治则编码
        chineseMedicineCode: this.bl_chineseMedicineCode, // 中医病名编码
        westernMedicineCode: this.bl_westernMedicineCode, // 西医病名编码
        fangJiSubCode: this.prescriptionCode, //  （即prescriptionCode） 方剂编码
        fangJiSubName : this.prescriptionName, // （即prescriptionName ）方剂名称
        effect: this.bl_effect, // 功效
        attend : this.bl_attend, // 主治
        calcite: this.bl_calcite, // 方解
        source : this.bl_source, // 来源
        usageCount: this.bl_usageCount, // 付数
        advice : this.bl_yizhu, // 医嘱
        remark : this.bl_yongfa_window.zhu, // 备注
        daiJian: this.checked?true:false, // 中药是否代煎 0或1
        fangjiUsage : this.bl_yongfa, // 服法
        fangJiDetailList: this.chufangItemData,
      }
    },
    bl_yongfa:function(){ // 用药方法
      let ri = this.bl_yongfa_window.ri?(this.bl_yongfa_window.ri + '日 ' ):''
      let fu = this.bl_yongfa_window.fu?(this.bl_yongfa_window.fu + '付 ' ):''
      let ci = this.bl_yongfa_window.ci?(this.bl_yongfa_window.ci + '次 '):''
      let rmk = this.bl_yongfa_window.zhu?(' 备注: ' + this.bl_yongfa_window.zhu):''
      return ri+fu+ci+rmk
    },
    hpwrData:function(){ // {han: 0, re: 0, wen: 1, ping: 0}
      let han = this.chufangResData.han || 0
      let ping = this.chufangResData.ping || 0
      let re = this.chufangResData.re || 0
      let wen = this.chufangResData.wen || 0
      // let total = (han + ping + re + wen) || 1
      let total = this.chufangItemData.length || 1
      return `寒${han}味${fun.GetPercent(han,total)}   热${re}味${fun.GetPercent(re,total)}   温${wen}味${fun.GetPercent(wen,total)}   平${ping}味${fun.GetPercent(ping,total)}`
    },
    chufangItemData:function () {
      return [...this.yaofangItem,...this.yaocaiItem]
    }
  },
  data() {
    return {
      tagWindowShow: true, // 标准标签切换
      opType: '1', // 操作类型(草药添加1/修改0)
      lzData: [], // 论治表格数据
      yaofangItem: [], // 药方-> 药材列表
      rowSign: {}, // 当前选择列
      // 下面是zhijie数据
      yaocaiItem: [], // 添加->药材列表
      dialog_caoyaotianjia: false, // 弹窗 草药添加
      dialog_caoyao: false, // 弹窗 草药
      dialog_yongfa: false, // 弹窗 用法
      dialog_xiyao: false, // 弹窗 西药
      dialog_chineseMedicine: false, // 弹窗 选择中医病名
      dialog_yizhu: false, // 弹窗 医嘱
      searchZY: '', // 中医搜索字段
      searchXY: '', // 西医搜索字段
      caoyao: {// 草药数据(添加窗口)
        JFlag: 1,
        CFlag: 0,
        ZFlag: 0,
        SFlag: 0,
      },
      zy_mainSymptomName: '', // 主症
      zy_diseaseName: '', // 兼症
      zy_tongueName: '', // 舌相
      zy_pulseName: '', // 脉相
      yaoxingData: 1, // 君臣佐使 药性默认
      yaojie: [], // 药解
      yaojieActive: '',
      fangjie:{}, // 方解
      fangjieActive: '',
      prescriptionName: '', // 方剂名称
      prescriptionCode: '', // 方剂编码
      checked: false, // 是否代煎药
      bl_usageCount: '', // 药方付数
      zyData:[], // 中医病名表格数据
      xyData:[], // 西医病名表格数据
      yzData: [], // 医嘱表格数据
      caoyaoData: [], // 草药选择数据
      caoyaoSelectQuery: '', // 草药选择查询条件
      checkList: [], // 性味勾选
      checkFlage: '', // 药性 标签
      caoyaoYaoXing: [], // 草药药性
      remark: '', // 备注
      chufangQueryData: {drugCode: '',prescriptionCode: ''}, // 草药/处方查询扩展信息
      chufangResData:{}, // 草药/处方查询扩展信息结果
      jianyaoOptions:[],  // 煎药方法
      temp:{}, // 临时测试 绑定君臣佐使等...
      bl_chineseMedicineName: '', // 病例 中医病名
      bl_chineseMedicineCode: '', // 病例 中医编码
      bl_westernMedicineName: '', // 病例 西医病名
      bl_westernMedicineCode: '', // 病例 西医编码
      bl_prescriptionName: '', // 病例_处方名
      bl_prescriptionCode: '', // 病例_处方名
      bl_attend: '', // 处方主治
      bl_calcite: '', // 方解
      bl_source: '', // 来源
      bl_effect: '', // 功效
      bl_yizhu: '', // 病例 医嘱
      imgSrc: '../../static/img/nopic.png', // 图片
      bl_yongfa_window: {
        ri: '', // 日
        fu: '', // 付
        ci: '', // 次
        zhu: '' ,// 备注
      }, //
    }
  },
  methods:{
    handleAdd(){ // 添加草药窗口
      this.caoyao = {}
      this.opType = 1
      this.getJianYaoData()
      this.dialog_caoyaotianjia = true
    },
    handleAddCaoyao(){ // 打开选择草药窗口
      this.dialog_caoyao = true
      this.handelSearchCY()
    },
    jingyanAddData(data,item){ // 下部按钮经验组件
      this.lzData = this.lzData || []
      item.subID="经验方"
      this.lzData.push(item)
    },
    handelSearchCY(search){ // 获取草药数据
      let  drugTaste = '' , drugMeridian = ''
      if(this.checkFlage == '0'){
        drugTaste = search
      }
      if(this.checkFlage == '1'){
        drugMeridian = search
      }
      getDrugDictList({
        drugModelCode: '2',
        stopFlag: 0,
        drugTaste: drugTaste,
        drugMeridian: drugMeridian,
        inputCode: this.caoyaoSelectQuery,
      }).then(res=>{
        this.caoyaoData = res.data.data
      })
    },
    handelGetYaoJie(row){ // 获取药解
      if(this.yaojieActive == row.drugCode){
        return
      }
      this.yaojie = []
      this.yaojieActive = row.drugCode
      getDrugExplanation(row.drugCode).then(res=>{
        if(res.data.code == '0'){
          this.yaojie = res.data.data
        }
      })
    },
    handelTagChange(e,v){ // 处方/标准切换
      document.querySelector('.tag-select .active').className = ''
      e.target.className='active'
      if(v){
        this.tagWindowShow = false
      }else{
        this.tagWindowShow = true
      }
    },
    handleCayoYaoEdit(row){ // 处方草药编辑
      this.caoyao=row
      this.opType = 0
      this.getJianYaoData()
      this.dialog_caoyaotianjia = true
    },
    radioChange(value){ // 君臣选择
      this.caoyao.JFlag = this.caoyao.CFlag = this.caoyao.ZFlag= this.caoyao.SFlag = 0
      switch (value) {
        case 1:
          this.caoyao.JFlag = 1
          break;
        case 2:
          this.caoyao.CFlag = 1
          break;
        case 3:
          this.caoyao.ZFlag = 1
          break;
        case 4:
          this.caoyao.SFlag = 1
          break;
        default:
          this.caoyao.JFlag = 1
          break;
      }
    },
    handelYaoFangTableSelection(item,row){ // 药方表列选择
      this.$refs.yfTable.clearSelection()
      if(this.rowSign === row){
        this.rowSign = {}
        this.yaofangItem = []
        this.prescriptionName = ''
        return
      }
      this.prescriptionName = row.prescriptionName
      this.prescriptionCode = row.prescriptionCode
      this.rowSign = row
      this.$refs.yfTable.toggleRowSelection(row)
      getDrugByPrescriptionCode({prescriptionCode: row.prescriptionCode}).then(res=>{
        if (res.data.code==='0') {
          this.yaofangItem = res.data.data
        }else{
          this.$message.error('获取药材失败')
        }
      })
    },
    handelGetFangJie(row){ // 获取方解
      if(this.fangjieActive == row.prescriptionCode){
        return
      }
      this.fangjie = []
      this.fangjieActive = row.prescriptionCode
      getCalciteByPrescriptionCode({prescriptionCode: row.prescriptionCode}).then(res=>{
        if(res.data.code == '0'){
          this.fangjie = res.data.data[0]
        }
      })
    },
    handelAddCaoYaoItem(){ // 添加草药窗口 确定按钮
      if(this.caoyao.drugCode == '' || !this.caoyao.quanitity){
        this.$message.warning("请选择填写完整药材信息")
        return
      }
        this.dialog_caoyaotianjia = false
        if(this.opType===1){
          this.yaocaiItem.push({...this.caoyao})
          this.handelYaoFangExpData()
      }
    },
    getJianYaoData(){ // 获取煎药方法列表
      if(this.jianyaoOptions.length>0){
        return
      }
      getSufferType().then(res=>{
        if(res.data.code==='1'){
          this.jianyaoOptions = res.data.data
        }
      })
    },
    handelYaoFangExpData(){ // 获取草药/药房 图表/寒热等扩展信息
      let drugCode = ''
      this.chufangItemData.forEach(element => {
        drugCode += element.drugCode + ';'
      });
      this.chufangQueryData.drugCode = drugCode
      if(this.chufangQueryData.drugCode == '' && this.chufangQueryData.prescriptionCode==''){
        //this.$refs['echart'].ecinit()
        this.$refs['echart'].updata([],1)
        this.chufangResData={wen:0,han:0,ping:0,re:0}
        return
      }
      this.chufangQueryData.drugCode = drugCode
      forHRWPData(this.chufangQueryData).then(res=>{
        this.chufangResData = res.data.data
      })
      forDTZXTData(this.chufangQueryData).then(res=>{
        this.$refs['echart'].updata(res.data.data,1)
      })
    },
    handelTypeChange(e){ // 草药选择页 标签切换
      document.querySelector(".tab-btn .tab-btn-active").className = ''
      e.target.className = "tab-btn-active"
      this.checkFlage = e.target.dataset.index
      this.caoyaoSelectQuery = ''
      this.caoyaoYaoXing = []
      this.checkList = []
      this.handleGetYaoXing()
      this.handelSearchCY()
    },
    select_item(row){ // 草药选择双击事件
      let flag = true // 防止草药重复添加
      this.chufangItemData.filter(item=>{
        if(item.drugCode == row.drugCode){
          this.$message.warning('重复选择 列表中已存')
          flag = false
        }else{
          flag = true
        }
      })
      if(flag){
      this.caoyao.drugName = row.drugName
      this.caoyao.drugCode = row.drugCode
      this.caoyao.drugTaste = row.drugTaste
      this.dialog_caoyao = false
      }
    },
    handeChineseMedicine(){ // 中医病名选择窗口
      this.dialog_chineseMedicine = true
      this.handelSearchZY()
    },
    handelSearchZY(){ // 查询中医病名数据
      searchChineseMedicineList({searchText: this.searchZY}).then(res=>{
        this.zyData = res.data.data
      })
    },
    handelSelectZYi(row){ // 中医 双击选中
      this.bl_chineseMedicineName = row.chineseMedicineName
      this.bl_chineseMedicineCode = row.chineseMedicineCode
      this.dialog_chineseMedicine = false
    },
    handeWesternMedicine(){ // 西医病名选择窗口
        this.dialog_xiyao = true
        this.handelSearchXY()
    },
    handelSearchXY(){ // 查询西医病名数据
      getAllWesternMedicineName({searchText: this.searchXY}).then(res=>{
        this.xyData = res.data.data
      })
    },
    handelSelectXiYi(row){ // 西医 双击选中
      this.bl_westernMedicineName = row.westernMedicineName
      this.bl_westernMedicineCode = row.westernMedicineCode
      this.dialog_xiyao = false
    },
    handelYIzhu(){ // 医嘱弹窗页面
      this.dialog_yizhu = true
      this.handelSearchYiZhu()
    },
    handelSearchYiZhu(){ // 查询医嘱
      getModelTable({
        usedPhrasesType: '70', // 70 获取医嘱
        deptCode: sessionStorage.getItem('current_deptCode'), // 科室类别 后期修改
      }).then(res=>{
        this.yzData = res.data.data
      })
    },
    handelAddYizhu(row){ // 添加医嘱到模板
      this.bl_yizhu += row.usedPhrasesContent + " "
    },
    handleGetYaoXing(){ // 草药药性分类列表 flag: 0性味药 / 1引经药

      getTasteOrMeridian({
        flag: this.checkFlage,
        }).then(res=>{
          this.caoyaoYaoXing = res.data.data
          this.xuanran = true
      })
    },
    handelYaoXingChange(e){ // 草药药性更改
      this.handelSearchCY(this.checkList.join(','))
    },
    handleYongfa(){ // 用法弹窗
      this.dialog_yongfa = true
    },
    handleRemoveCYitem(index,rows){ // 药材列表 删除操作
      if(index<this.yaofangItem.length && this.yaofangItem.length!=0){
        this.yaofangItem.splice(index, 1)
      }else{
        this.yaocaiItem.splice(index-this.yaofangItem.length,1)
      }
    },
    handleRemove(){ // 删除草药

    },
    test(){
      console.log("test")
    }
  },
  mounted(){
    getGovernanceResult({
      empBasicMainID: this.empBasicMainID,
      operationType: '1',
      // cureCode: this.pro_cureCode,
      // dialecticalCode: this.pro_dialecticalCode
    }).then(res=>{
      this.lzData = res.data.data
    })
  },
  created(){
    this.zy_mainSymptomName=sessionStorage.getItem('zy_mainSymptomName') || ''
    this.zy_diseaseName=sessionStorage.getItem('zy_diseaseName') || ''
    this.zy_tongueName=sessionStorage.getItem('zy_tongueName') || ''
    this.zy_pulseName=sessionStorage.getItem('zy_pulseName') || ''
    this.imgSrc=sessionStorage.getItem('zy_imgSrc') || ''
  }
}
</script>
<style scoped>
.tag-select span{
  display: inline-block;
  margin-right: 10px;
  padding: 0 6px;
  height: 26px;
  width: auto;
}
.tag-select span:hover,.tag-select .active{
  border-bottom: 2px solid #BF7045;
  color: #BF7045;
  cursor: pointer;
}
.border-box{
  padding-top: 8px;
  border: 1px solid #eee;
}
.chufang{
  margin-top: 10px;
  padding-top: 16px;
  color: #333333;
  border: 1px solid #EAEAEA;
}
.chufang-center{
  font-size: 14px;
}
/* 表内图标 */
.chufang-icon{
  display: block;
  cursor: pointer;
  width: 18px;
  height: 21px;
}
#main{
  margin-top: 32px;
  border: 1px solid #333;
}
.tab-btn span{
  display: inline-block;
  height: 40px;
  width: 50px;
  margin-right: 20px;
  font-size: 16px;
  color: #333;
  line-height: 40px;
  text-align: center;
  cursor: pointer;
}
.tab-btn-active{
  color: #BF7045;
  border-bottom: 2px solid #BF7045;
}
.yizhu-title{
  text-align: center;
}
.select-caoyao{
  margin: 8px 0;
  border: 1px solid #ccc;
}
.caoyao-title{
  padding: 8px;
}
.caoyao-item{
  padding: 5px;
  background-color: #FAF7F6;
}
/* 草药药性列表 首行左缩进对齐 */
.caoyao-yaoxing-list label:first-child{
  margin-left: 31px;
}
</style>





